.App {
  background-color: #282c34;
  color: white;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: fixed;
}

button {
  margin: 0 25px;
}

.Header {
  text-align: center;
}

.Block {
  border-radius: 15px;
  border: 2px solid white;
  display: inline-block;
  margin: 10px;
}

.Control {
  width: 60vw;
  padding: 10px;
}

.Side {
  position: absolute;
  height: 50vh;
  width: 15%;
}

.Left {
  left: 0;
}

.Right {
  right: 0;
}

.Bottom {
  position: absolute;
  height: 20vh;
  left: 0;
  right: 0;
  bottom: 20px;
  margin: 0 auto;
  width: 50vw;
}
